.mountain {
    width: 100%;
    height: 100%;
    background-image: url("../../../../assets/images/parallaxSlides/mountainSlide/mountains.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: top;
    background-clip: border-box;
    background-attachment: fixed;
}

.alive {
    position: absolute;
    bottom: 0;
    width: 100%;
    height: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    color: white;
    font-size: 20em;
    letter-spacing: 5px;
}

.alive span {
    margin: 0 70px;
}

.man {
    position: absolute;
    width: 100%;
    height: 60%;
    bottom: 0;
    background: url("../../../../assets/images/parallaxSlides/mountainSlide/man.png");
    background-repeat: no-repeat;
    background-size: auto 100%;
    background-position: center;
}


.cloudsParent {
    position: absolute;
    width: 100%;
    height: 100%;
    top: 0;
    overflow: hidden;
}

.clouds {
    width: 150%;
    opacity: 1;
    mix-blend-mode: soft-light;
}